body {
  color: #212121;

  font-family: Roboto, sans-serif;
  opacity: 0.7;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 26px;
  padding-right: 26px;
}
